.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}
.MuiFab-primary{background-color: black!important;}
@media (prefers-reduced-motion: no-preference) {
  .App-logo {
   /* animation: App-logo-spin infinite 1s linear; */
   margin:0;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}


.page{
  position: absolute;
  top:80px;
  left:0;
  right:0;
}
/* simple - enter transition 300ms, exit 150ms */
.fade-appear,
.fade-enter {
  opacity: 0.01;
  transform: scale(1.1);
}
.fade-appear-active,
.fade-enter.fade-enter-active {
  opacity: 1;
  transform: scale(1);
  transition: all 300ms;
}

.fade-exit {
  opacity: 1;
  transform: scale(1);
}

.fade-exit.fade-exit-active {
  opacity: 0.01;
  transform: scale(1.1);
  transition: all 300ms;


}
.navWrapper{margin-top:20px; margin-bottom:40px;}
.MuiButtonBase-root:focus{background: #fff !important; color:#000}
a{color:#000}
.MuiListItem-root a { text-align: center; width:100%}
@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
